<script>
import Header from '../../../../components/school-training/Header.vue'
import HorizontalTap from '../../../../components/tap/HorizontalTap.vue'
import PDF from '../../../../components/learning-library/pdf'
export default {
  name: 'app',
  components: {
    Header,
    HorizontalTap,
    PDF
  },
  mounted () {
    console.log(document.body.clientWidth)
    this.loadData()
  },
  data () {
    return {
      taskList: null,
      data2: [{
        name: '执行任务',
        url: 'task-list.html',
        type: '_self'
      }, {
        name: '任务单',
        url: 'work-order.html',
        type: '_self'
      }, {
        name: '日报任务',
        url: 'daily-tasks.html',
        type: '_self'
      }, {
        name: '相关资料',
        url: 'learning-library.html',
        type: '_blank'
      }, {
        name: '成长轨迹',
        url: '',
        type: '_self'
      }],
      activities: [],
      pdfFlg: false,
      pdfUrl: ''
    }
  },
  methods: {
    loadData () {
      let me = this
      this.$http.post('/doproject/steplist', {
        'teamid': localStorage.getItem('teamId')
      }, res => {
        console.log(res)
        me.taskList = res.data.stages
        me.formatData()
      })
    },
    formatData () {
      let obj = {}
      this.activities = []
      for (var i = 0; i < this.taskList.length; i++) {
        obj = { content: this.taskList[i].stageName, timestamp: '', color: '#FF6600', size: 'large', icon: '', dotType: 'stage' }
        this.activities.push(obj)
        for (var j = 0; j < this.taskList[i].steps.length; j++) {
          obj = { content: this.taskList[i].steps[j].stepName, timestamp: '2018-04-03', icon: 'el-icon-check', color: '#0bbd87', size: 'normal', dotType: 'step', progress1: Math.floor(Math.random() * 100), progress2: Math.floor(Math.random() * 100), progress3: Math.floor(Math.random() * 100) }
          this.activities.push(obj)
        }
      }
    },
    customColorMethod (percentage) {
      if (percentage < 20) {
        return 'rgb(19, 206, 102)'
      } else if (percentage <= 80 && percentage >= 20) {
        return '#6612cf'
      } else {
        return 'rgb(255, 73, 73)'
      }
    },
    goPdf () {
      this.pdfFlg = false
      this.$nextTick(() => {
        this.pdfUrl = 'http://39.100.37.131/file/pdffile/1560492651790$_$问题列表_答案.docx.pdf'
        this.pdfFlg = true
      })
    },
    closePdf () {
      this.pdfFlg = false
    }
  }
}
</script>
